.jumbotron{
  position: relative;
  z-index:-2;
}

#video-background {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: -1;
  width:100%;
}


.jumbotron .titan-caption {
  padding: 20rem;
}

.hide {
  display: none;
}

.hidden {
  display: none;
}

.product h4 {
      font-size: 1rem;
      color: $black;
}

.product a h4:hover {
      text-decoration: none;
}

.woocommerce-pagination {
  display: none;
}

.eboywp-counter {
  display: none;
}
img.ico {
  cursor: pointer;
  height: 1.5rem;
}

.btn-outline-primary:hover img.ico {
    fill: darkslateblue;
}

.btn {
  border-radius: 0rem;
}

@media (min-width: 768px) {
  .collapse.dont-collapse-sm {
    display: block;
    height: auto !important;
    visibility: visible;
  }
}

.row-height{
  height: 100vh;
}

/*Set up the columns with a 100% height, body color and overflow scroll*/

.left{
  background-color: $white;
  height: 100%;
  overflow-y: scroll;
}

.right{
  background-color: $white;
  height: 100%;
  overflow-y: scroll;
}

/*Remove the scrollbar from Chrome, Safari, Edge and IE*/
::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

* {
  -ms-overflow-style: none !important;
}

.card-img-overlay {
    padding: .5rem;
    width: 20px;
height: 20px;
}
.woocommerce-Tabs-panel--description {
  text-align: justify;
      text-justify: inter-word;
      word-spacing: .011rem;
      letter-spacing: .035rem;
      line-height: 1.5rem;
}

.woocommerce-Tabs-panel p {
  margin-bottom: 0;
}

.title-unzoomed {
  display: none;
}

.title-zoomed {
  display: block;
  height: 100vh;
}

