.btn.btn-border-w {
  background: transparent;
border: 1px solid hsla(0, 0%, 100%, .75);
color: #fff;
}

.btn.btn-border-w:hover {
  background: #fff;
border-color: transparent;
color: #111;
}

.btn.btn-border-g {
  background: transparent;
border: 1px solid $primary;
color: $primary;
}

.btn.btn-border-g:hover {
  background: $gray-900;
border-color: transparent;
color: $primary;
}


.feedback {
  background-color : $primary;
  color: white;
  padding: 10px 20px;
  border-radius: 4px;
  border-color: $primary;
}

.mybutton_vertical {
  position: fixed;
  transform: rotate(-90deg);
  bottom: 50vh;
  left: -90px;
  z-index: 1999;
}

.mybutton_horizontal {
  position: fixed;
  transform: rotate(0deg);
  bottom: 0vh;
  right: 0px;
  z-index: 1999;
}

.btn-outline-secondary {
    color: $black;
    background-color: transparent;
    background-image: none;
    border-color: $black;
}

.tinvwl_add_to_wishlist_button  {
