// Grid settings
$main-sm-columns:       12;
$sidebar-sm-columns:    4;



$white: #fff !default;
$white_form: #fcfcfc !default;
$white-50:  rgba(255,255,255,0.5);
$gray: #ccc !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #f6f6f6 !default;
$gray-600: #868e96 !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #1a1a1a !default;
$black: #000 !default;
$alert-danger-text:    #868e96;
$alert-form:  #bf6c79;

$primary: #99894e !default;
$secondary: #f3f0ed !default;
$link-color: $primary!default;
$navbar-light-hover-color: $primary;

@mixin body-font {
  font-family: 'museosansregular300', sans-serif;
  color: $gray-900;
  -webkit-font-smoothing: antialiased;
  letter-spacing: .06rem;
  font-size: 1rem;
}

@mixin map-h-font {
  font-family: 'museosansregular300', sans-serif;
  color: $white;
  -webkit-font-smoothing: antialiased;
  letter-spacing: .06rem;
  font-size: 1.5rem;
  padding: 0rem 0 1rem 0rem ;
}


.w-24 {
    width: 24.10%!important;
}

.w-32 {
    width: 32.33333333%!important;
}

.w-48 {
    width: 48%!important;
}

.w-47 {
    width: 47%!important;
}
.w-49 {
    width: 49%!important;
}

.w-96 {
    width: 96%!important;
}
