.eboywp-page {
display: inline-block;
padding: 0px 4px;
margin-right: 6px;
cursor: pointer;
}

.eboywp-page.active {
font-weight: bold;
cursor: default;
}

.eboywp-loading {
width: 20px;
height: 20px;
background: url(../images/loading.png) no-repeat;
background-size: 20px 20px;
-webkit-animation: spin 700ms infinite linear;
-moz-animation: spin 700ms infinite linear;
animation: spin 700ms infinite linear;
}

.eboywp-overlay {
position: absolute;
background-color: #fff;
opacity: 0.6;
}

/* Checkboxes */

.eboywp-type-checkboxes .eboywp-depth {
display: none;
}

.eboywp-type-checkboxes .eboywp-depth.visible {
display: inherit;
}

.eboywp-checkbox {
background: url(../images/checkbox.png) 0 50% no-repeat;
background-size: 14px 14px;
margin-bottom: 4px;
padding-left: 20px;
cursor: pointer;
}

.eboywp-checkbox.checked {
background-image: url(../images/checkbox-on.png);
}

.eboywp-checkbox.disabled,
.eboywp-radio.disabled {
opacity: 0.4;
cursor: default;
}

.eboywp-checkbox .eboywp-expand {
float: right;
}

/* Radio */

.eboywp-radio {
background: url(../images/radio.png) 0 50% no-repeat;
background-size: 14px 14px;
margin-bottom: 4px;
padding-left: 20px;
cursor: pointer;
}

.eboywp-radio.checked {
background-image: url(../images/radio-on.png);
}

/* Date range */

.flatpickr-clear {
text-align: center;
padding: 5px 0;
cursor: pointer;
}

/* fSelect */

.eboywp-type-fselect .fs-wrap,
.eboywp-type-fselect .fs-dropdown {
width: 220px;
}

.eboywp-type-fselect .fs-wrap.fs-disabled .fs-option {
opacity: 0.4;
cursor: default;
}

/* Hierarchy */

.eboywp-depth {
margin-left: 12px;
}

.eboywp-link {
cursor: pointer;
}

.eboywp-link.checked {
font-weight: bold;
cursor: default;
}

.eboywp-toggle {
cursor: pointer;
}

.eboywp-hidden {
display: none;
}

/* Slider */

.eboywp-slider-wrap {
padding-bottom: 15px;
}

.eboywp-slider-reset {
border: 1px solid #d9d9d9;
border-radius: 3px;
background: #fff;
box-shadow: inset 0 0 1px #fff, inset 0 1px 7px #ebebeb, 0 3px 6px -3px #bbb;
padding: 4px 8px;
cursor: pointer;
}

.noUi-target {
padding: 0 15px;
}

/* Search */

.eboywp-search-wrap {
position: relative;
}

.eboywp-search {
padding-right: 28px;
}

.eboywp-btn {
top: 0;
right: 5px;
line-height: 1;
position: absolute;
cursor: pointer;
opacity: 0.5;
}

.eboywp-btn:before {
display: inline-block;
content: '';
width: 20px;
height: 20px;
background: url(../images/icon-search.png) no-repeat;
background-size: 20px 20px;
}

.eboywp-btn.f-reset:before {
background: url(../images/icon-close.png) no-repeat;
background-size: 20px 20px;

}

/* Proximity */



input.eboywp-radius {
  width: 100%;
}

/* CSS animations */

@-o-keyframes spin {
from {
    -o-transform: rotate(0deg);
}
to {
    -o-transform: rotate(360deg);
}
}

@-moz-keyframes spin {
from {
    -moz-transform: rotate(0deg);
}
to {
    -moz-transform: rotate(360deg);
}
}

@-ms-keyframes spin {
from {
    -ms-transform: rotate(0deg);
}
to {
    -ms-transform: rotate(360deg);
}
}

@-webkit-keyframes spin {
from {
    -webkit-transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
}
}

@-keyframes spin {
from {
    transform: rotate(0deg);
}
to {
    transform: rotate(360deg);
}
}

/* Selections shortcode */

.eboywp-selections li {
display: inline-block;
line-height: 1;
}

.eboywp-selections .eboywp-selection-value {
display: inline-block;
margin-right: 10px;
cursor: pointer;

padding-right: 16px;
background-image: url(../images/icon-close.png);
background-size: 12px 12px;
background-repeat: no-repeat;
background-position: right center;
}
