  font-weight: normal;
  @extend .form-group;
}
.search-form .search-field {
  @extend .form-control;
}
.search-form .search-submit {
  @extend .btn;
  @extend .btn-secondary;
}

select {
  width: 100%;
}

.eboywp-type-fselect .fs-wrap, .eboywp-type-fselect .fs-dropdown {
    width: 100%!important;
}


