.gfield_error .warningTextareaInfo {color:white;}
.gfield_error .ginput_container_fileupload {
    color: black;
}

.gfield_error img.add_list_item {
    background-color: white;
    border-radius: 50%;
}

/* From Heading */

.gform_heading .gform_description {
    padding: 1em 0;
    display: block;
}

/* Progress Bars & Paging */

.gf_progressbar {
    position: relative;
    border-top: 5px solid #efefef;
}
.gf_progressbar_percentage {
    color: white;
    border-top: 5px solid #4480c3;
    position: relative;
    top: -5px;
    text-align:right;
}
.gf_progressbar_percentage span {
    background: black;
    display: inline-block;
    width: 2.5em;
    height: 2.5em;
    line-height: 2.75;
    border-radius: 3px;
    border-top-right-radius:0;
    font-size: .9em;
    margin-top: .25em;
    position:relative;
    text-align:center;
}
.gf_progressbar_percentage.percentbar_0 span {border-top-right-radius:3px; border-top-left-radius:0;}
.gf_progressbar_percentage span:after {content:""; width:0; height:0; position:absolute;
	border-left:5px solid transparent; border-right:0 solid transparent; top:-5px; right:0;
	border-bottom:5px solid black;}
.gf_progressbar_percentage.percentbar_0 span:after {
    right: auto;
    left:0;
    border-right:5px solid transparent;
    border-left:0;
}
.gf_progressbar_percentage.percentbar_red {border-color:#bf0421;}
.gf_progressbar_percentage.percentbar_green {border-color:green;}
.gf_progressbar_percentage.percentbar_gray {border-color:#666;}
.gf_progressbar_percentage.percentbar_orange {border-color:#e8641b;}
.gf_progressbar_percentage.percentbar_custom {border-color:transparent;}



.gf_progressbar_wrapper + .gform_body .gform_page_footer {
    padding-top: 2em;
}

.gform_next_button {
    float: right;
}

div.gform_confirmation_message {
    background: green;
    color: white;
    padding: 1em;
    margin: 1em 0;
    border-radius: 3px;
}

body img.gform_ajax_spinner {display:block; margin:1em auto;}

.gf_page_steps {
    display: table;
    width: 100%;
}
.gf_page_steps > div {
    padding: .25em;
    display: table-cell;
    color: white;
    background: black;
    vertical-align: top;
    font-weight: bold;
}
.gf_page_steps > div.gf_step_pending {background:#efefef; color:#666;}
.gf_step_clear {display:none !important;}
span.gf_step_number {
    background: white;
    color: black;
    display: inline-block;
    width: 2em;
    text-align: center;
    border-radius:50%;
    line-height:2;
}

@media (max-width:767px) {
	.gf_step {text-align: center;}
	.gf_step span {
	    clear: both;
	    display: block;
	    margin: 0 auto;
	}
	span.gf_step_number {
	    margin-top: .5em;
	}
	span.gf_step_label {
	    margin-top: -1em;
	}
}

/* Credit Card */

.ginput_container_creditcard {
    background: #efefef;
    padding: 2em;
    border-radius:5px;
}
.ginput_container_creditcard input, .ginput_container_creditcard select {
    background: white;
}

.gform_card_icon_container div {
